import React from 'react'
import {Provider }from 'react-redux'
import store from './redux/store'
import './App.css';
import Main from './Main'
class App extends React.Component{
    render(){
        return(
            // providerstore[]
            // {store}store  mapStateToProps(state)state
            <Provider store={store}>
                <div >
                    <Main/>
                </div>
            </Provider>

        )
    }
}
export default App



